<template>
  <div class="login-page">
    <div class="login-page">
      <img src="@/assets/login.png" alt="" class="imglogin">
      <van-form @submit="onSubmit">
        <van-field
          v-model="username"
          name="username"
          label="用户名"
          placeholder="用户名"
          :rules="[
            { required: true, message: '请填写用户名' },
            {pattern:/^\w{5,}$/,message: '用户名必须5位以上'}]"
        />
        <van-field
          v-model="password"
          type="password"
          name="password"
          label="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' },
            {pattern:/^\w{6,}$/,message: '密码必须6位以上'}
          ]"
        />
        <div style="margin: 16px;">
         <van-button block type="info" native-type="submit" round="true">登录</van-button>
        </div>
        <router-link class="link" to="register">无账号，去注册</router-link>
      </van-form>
    </div>
  </div>
</template>

<script>
import { login } from '@/api/user'
import { setToken } from '@/utils/storage'
export default {
  name: 'LoginPage',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    async onSubmit (values) {
      const res = await login(values)
      // console.log(res)  保存token到本地
      // localStorage.setItem('hmmj-vant-token', res.data.data.token)
      setToken(res.data.token)
      // getToken()
      this.$toast.success('登录成功') // 登录成功，提示用户成功
      this.$router.push('/') // 跳到首页
    }
  }

}
</script>

<style lang="less" scoped>
.link{
    color: #069;
    font-size: 12px;
    padding-right: 20px;
    float: right;
}
.imglogin{
  width:100%;
  height:300px;
}

</style>
